import React, { Component } from "react";
import PropTypes from "prop-types";
import { hot } from "react-hot-loader";
import axios from "axios";
import Header from "./Header.jsx";

//[TBD] Adjust this function, to get numbers in less than 1000 for example
// Newly added: get random integer between 2 numbers (e.g. 1 and 100K)
// Note: inclusive for both numbers
function getRandomInt(min, max) {
  min = Math.ceil(min);
  max = Math.floor(max + 1);
  return Math.floor(Math.random() * (max - min)) + min;
}

const getArtistInfo = function(id, callback) {
  // let id = getRandomInt(1, 100000);
  axios
    .get(`/artists/${id}`)
    .then(response => {
      callback(response.data); //data[0]
    })
    .catch(error => {
      console.error(error);
    });
};
const getLocationsInfo = callback => {
  axios
    .get(`/locations/${id}`)
    .then(response => {
      callback(response.data);
    })
    .catch(error => {
      console.error(error);
    });
};

const blackBG = { backgroundColor: "#191414" };

class App extends Component {
  constructor() {
    super();
    this.state = {
      // artistDisp: null
      artistID: null,
      artistName: null,
      followed: null,
      followersNumber: null,
      verified: null,
      Biography: null,
      artistImages: [],
      locations: {} // From table 2, locations
    };
    this.handleFollowToggle = this.handleFollowToggle.bind(this);
  }

  componentDidMount() {
    let id = getRandomInt(1, 100000);
    getArtistInfo(id, function(data) {
      // this.setState({ artistDisp: data });
      this.setState({
        artistID: data.artistID,
        artistName: data.artistName,
        followed: data.followed,
        followersNumber: data.followersNumber,
        verified: data.verified,
        Biography: data.Biography,
        // Convert from string to array
        artistImages: data.artistImages.slice(1, data.artistImages.length - 2)
      });
    });
  }

  handleFollowToggle(event) {
    event.preventDefault();
    // console.log('###FOLLOW CLICK###');
    this.setState(prevState => {
      // NOTE: trick when you want to update a part of an object
      return {
        artistDisp: {
          ...prevState.artistDisp,
          followed: !prevState.artistDisp.followed
        }
      };
    });
  }

  render() {
    const toRender = !!this.state.artistID ? (
      <Header
        artistName={this.state.artistName}
        artistImages={this.state.artistImages}
        followersNumber={this.state.followersNumber}
        handleFollowToggle={this.handleFollowToggle}
      />
    ) : (
      <div className="placeHolder" />
    );
    return <React.Fragment>{toRender}</React.Fragment>;
  }
}

export default hot(module)(App);
